<style>
 @import "../assets/css/background_broadcrumb.css";
 
</style>
<template>
    <div>
       <div class="cover_service">
         <v-img :src="`${getHeader[0].images[0].link}`" cover>
            <v-overlay absolute class="bottom_card">
                <v-container>
                <v-card class="card_background" flat color="transparent" left>
                       <v-card-title class="title"><span>{{getHeader[0].title}}</span></v-card-title>
                        <v-card-subtitle class="desc">
                            <p>
                             {{getHeader[0].full_text}}
                            </p>
                        </v-card-subtitle>
                   </v-card>
                   <v-breadcrumbs :items="items2" class="broadcrumbs"></v-breadcrumbs>
                </v-container>
             </v-overlay>
          </v-img>
       </div>
       <section class="back_white">
         <v-img src="../assets/images/services/points-pattern-yellow.png" class="card_white">
                 <v-card flat color="transparent" class="card_service_text">
                     <v-card-subtitle class="desc_card">
                           <p>
                             {{getHeader[0].full_text}}
                           </p>
                     </v-card-subtitle>
                 </v-card>
          </v-img>
         <v-img :src="require('@/assets/images/services/transperant-yellow-points-wide-rec.png')"></v-img>
       </section>
       <section>
         
            <ServicesList></ServicesList>
        
       </section>
      
      
     
     </div>
  </template>
  <script>
  import ServicesList from '@/components/ServicesList2.vue';
  import { mapGetters, mapActions } from "vuex";
  export default{
   name:"ServiceView",
   components:{
     ServicesList
   },
   data(){
      return{
         items2: [
            {
               text: 'Home',
               disabled: false,
               href: '/',
            },
            {
               text: 'Services',
               disabled: false,
               href: '/services',
            },
         ]
      }
   },
    
   methods: {
    ...mapActions(["fetchTopics"]),
  },
  computed:{
   ...mapGetters(["getHeader"]),
  },
  mounted() {
   let servicesHeader={
    topic_type_id:30,
    cat:"header"
   }
   this.fetchTopics(servicesHeader);
 },
  }
  </script>