<style scoped>
  @import "../assets/css/servicesAbout.css";
  @import "../assets/css/serviceGeneral.css";
</style>
<template>
    <div>
    <div v-for="(service,index) in getListServices" :key="index">
    
    
       <div class="back_dark" >
        <v-layout align-start  :class="index%2==0?'flex-row':'flex-row-reverse exchange_col_order'">
            <div class="ch1">
                <div class="border_yellow me-2"></div>
                     
            </div>
          <div class="ch">
             <v-card class="card_service" flat color="transparent">
                <v-card-title   class="title_card">
                    <div class="d-flex">
                        <div>
                            <span>{{getListServices[index].title}}</span>
                
                        </div>
                     </div>
                   </v-card-title>
                <v-card-subtitle class="desc_card">
                    <p>
                        {{getListServices[index].full_text}} 
                    </p>
                 </v-card-subtitle>
             </v-card>
          </div>
         <div class="ch2">
            <div class="box_img">
                <v-img :src="`${getListServices[index].images[0].link}`"   max-width="976" max-height="960" class="img_card"></v-img>
                <v-img :src="require('@/assets/images/rect-yellow.png')"  max-width="520"  max-height="119" :class="index%2!=0?'rect_yellow2':'rect_yellow'">
                   <span class="text_breif_service"> {{getListServices[index].id}} </span>
                </v-img> 
            </div>
          </div>
         </v-layout>
       </div>
        <section :class="index==getListServices.length-1?'d-none':'rect_white_points'">
         <v-img :src="index%2==0?require('@/assets/images/services/rtl-transperant-yellow-points-wide-rec.png'):require('@/assets/images/services/transperant-yellow-points-wide-rec.png')">
          </v-img>
        </section>
        </div>
    
    </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
export default{
name:"ServicesAbout",
props:{
    reverse:String,
    keyCard:Number
},
 methods: {
    ...mapActions(["fetchTopics"]),
  },
  computed:{
    ...mapGetters(["getListServices"]),
    
  },
  mounted() {
   let services={
    topic_type_id:30,
    cat:"list"
   }
   this.fetchTopics(services);
 },
}

</script>